<template>
	<view class="category">
		<headerTab title="选择类目"></headerTab>
		<view class="category-piece">
			<text>其他</text>
			<text class="icon-shenglvehao iconfont" @click="handleClose()"></text>
		</view>

		<copyreader :show="compileShow" @close="handleClose()">
			<view class="operation" @touchstart="touch()" @click="amend()">
				修改
			</view>
			<view class="operation red">
				删除
			</view>
		</copyreader>
		<addPopup @close="append" :show="isShow" title="添加类目"></addPopup>
		<addOrder type="3" @click="append()"></addOrder>
	</view>
</template>

<script>
	import headerTab from '@/components/headerTab/index.vue';
	import addOrder from '@/components/addOrder.vue';
	import addPopup from '@/components/addPopup.vue';
	import copyreader from '@/components/copyreader/index.vue';
	export default {
		components: {
			headerTab,
			addOrder,
			addPopup,
			copyreader
		},
		props: {

		},
		data() {
			return {
				isShow: false,
				compileShow: "none",

			};
		},
		methods: {
			append() {
				this.isShow = !this.isShow
			},
			handleClose() {
				if (this.compileShow == 'none') {
					this.compileShow = 'show';
				} else {
					this.compileShow = 'hide';
					setTimeout(() => {
						this.compileShow = 'none';
					}, 200);
				}
			},
		}
	}
</script>

<style lang="scss">
	.category {
		background-color: #fff;

		.category-piece {
			border-bottom: 1rpx solid #ccc;
			display: flex;
			align-items: center;
			justify-content: space-between;
			padding: 14rpx 30rpx;

			.iconfont {
				color: #2ea469;
				background-color: #efeef3;
				padding: 4rpx 4rpx;
				border-radius: 2rpx;
			}
		}
	}
</style>
